var _ = require('lodash');
var marked = require('marked');
var classNames = require('classnames');
var React = require('react');
var ReactDOM = require('react-dom');

var Markdown = React.createClass({
  componentDidMount: function() {
    this.openLinksInNewWindow();
  },

  componentDidUpdate: function() {
    this.openLinksInNewWindow();
  },

  openLinksInNewWindow: function() {
    _.each(ReactDOM.findDOMNode(this.refs.markdown).getElementsByTagName('a'), function(el) {
      el.setAttribute('target', '_blank');
    });
  },

  render: function() {
    var classes = classNames('markdown', this.props.className);
    var md = '';
    var err = null;
    if (this.props.value) {
      try {
        md = marked(this.props.value);
      } catch (exc) {
        err = exc.toString();
      }
    }
    return (
      <div>
        <div ref="markdown" className={classes}
          dangerouslySetInnerHTML={{__html: md}} />
        <div className="text-danger">{err}</div>
      </div>
    );
  },
});

module.exports = Markdown;
